import React from 'react'
import { Link } from 'gatsby'

import { Col, Row} from 'react-bootstrap';

class Header extends React.Component {

  constructor(props){
    super(props);
    
    this.shrinkAfter = 50;
    this.state = {
      shrunken: false
    }
  };


  render(){
    const {siteTitle} = this.props;
    const {shrunken} = this.state;
    return(
      <>
        <div className="custom-nav-shadow" id="top">
  
        </div>
        <Row className={`custom-nav ${shrunken ? "navbar-scrolled" : ""}`}>
          <Col md={4} className={`custom-nav-brand ${shrunken ? "navbar-brand-scrolled" : ""}`}>
            <Link to="/"><h1>{siteTitle}</h1></Link>
          </Col>
          <Col md={8} className="custom-navbar-nav">
            <ul>
              <li><Link to="/#top">me</Link></li>
              <li><Link to="/#works">works</Link></li>
            </ul>
          </Col>
        </Row>
      </>
    )
  }

  componentDidMount(){
    document.addEventListener('scroll', () => {
        let toShrink = (window.scrollY > this.shrinkAfter);

        if(toShrink !== this.state.shrunken){
            this.setState({shrunken: toShrink});
        }
    });
  }
}

export default Header
